@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
/* base styles */
body {
  font-family: Poppins, sans-serif;
  margin: 0;
  font-size: 1.1em;
}
ul, li, p, h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}
ul {
  list-style-type: none;
}
.btn {
  background: none;
  border: 2px solid #1f9751;
  padding: 6px 12px;
  border-radius: 4px;
  color: #1f9751;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
.btn:hover {
  background: #1f9751;
  color: #fff;
}
.Home_container__3bftD {
    display: grid;
    grid-template-columns: 2fr 1fr;
    max-width: 960px;
    margin: 60px auto;
}

.Home_content__18qU0 {
    padding-right: 30px;
}

.Home_sidebar__21xz5 {
    padding-left: 30px;
}

.Home_sidebar__21xz5 form {
    padding: 20px;
    background: #1f9751;
    border-radius: 10px;
}


.Home_sidebar__21xz5 input, .Home_sidebar__21xz5 select {
    display: block;
    width: 100%;
    padding: 10px;
    margin-top: 8px;
    box-sizing: border-box;
    border: 0;
    border-radius: 4px;
    color: #555;
    font-size: 1em;

}


.Home_sidebar__21xz5 label {
    margin: 0 auto 20px;
    display: block;
    color: #fff;
}


.Home_sidebar__21xz5 button {
    color: #fff;
    border: 2px solid #fff;
    padding: 6px 12px;
    background-color: transparent;
    font-size: 1em;
    border-radius: 4px;
    cursor: pointer;
    display: block;
    width: 100%;
}

.Home_sidebar__21xz5 h3 {
    color: #1f9751;
    margin-bottom: 20px;
}

.Home_sidebar__21xz5 aside {
    margin-top: 40px;
    color: #555;   
}

.Home_sidebar__21xz5 aside li {
    margin: 10px;
}

/* Transactions */

.Home_transactions__3_tqr li {
    margin: 30px auto;
    border: 1px solid #f2f2f2;
    box-shadow: 3px 3px 5px rgba(50, 50, 50, 0.1);
